<template>
    <v-container class="pa-0" style="position: relative;">
        <v-app-bar
                app
                color="#FFFFFF"
                elevation="3"
                class="app-bar-gradient-back"
                dark
        >
            <v-row>
                <v-col class="text-left pa-0">
                    <v-btn text icon dark
                           tile
                           height="100%"
                           width="50px"
                           @click="$router.go(-1)">

                        <v-icon>mdi-arrow-left</v-icon>
                    </v-btn>
                </v-col>
                <v-col class="text-center">
                    <v-toolbar-title>{{permissionsText}}</v-toolbar-title>
                </v-col>
                <v-col>
                </v-col>
            </v-row>
        </v-app-bar>

        <v-card style="margin-top: 68px;">
            <v-list two-line active-class="primary--text" style="padding: 0px!important">
                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>
                            <v-row class="pa-0 ma-0">
                                <v-col class="pa-0 ma-0">
                                    <v-icon style="display: inline;  vertical-align: bottom">mdi-map-marker</v-icon>
                                    &nbsp;&nbsp;{{locationText}}
                                </v-col>
                                <v-col class="pa-0 ma-0 text-right" style="padding-top: 4px!important;">
                                    <span  v-if="locationPermissionGranted" class="green--text" style="font-size: 12px">
                                        {{grantedText.toUpperCase()}}
                                    </span>
                                    <span v-else class="red--text" style="font-size: 12px">
                                        {{notGrantedText.toUpperCase()}}
                                    </span>
                                </v-col>
                            </v-row>
                        </v-list-item-title>
                    </v-list-item-content>
                </v-list-item>

                <v-divider></v-divider>

                <v-list-item>
                    <v-list-item-content>
                        <v-list-item-title>
                            <v-row class="pa-0 ma-0">
                                <v-col class="pa-0 ma-0">
                                    <v-icon style="display: inline;  vertical-align: bottom">mdi-bell</v-icon>
                                    &nbsp;&nbsp;{{notificationsText}}
                                </v-col>
                                <v-col class="pa-0 ma-0 text-right" style="padding-top: 4px!important;">
                                    <span  v-if="notificationsPermissionGranted" class="green--text" style="font-size: 12px">
                                        {{grantedText.toUpperCase()}}
                                    </span>
                                    <span v-else class="red--text" style="font-size: 12px">
                                        {{notGrantedText.toUpperCase()}}
                                    </span>
                                </v-col>
                            </v-row>
                        </v-list-item-title>
                    </v-list-item-content>
                </v-list-item>
            </v-list>
        </v-card>

        <v-snackbar
                v-model="snackbar"
                :timeout="4000"
                style="bottom: 76px"
        >
            {{ snackbarText }}
            <v-btn
                    color="primary"
                    text
                    @click="snackbar = false"
            >
                {{closeText}}
            </v-btn>
        </v-snackbar>
    </v-container>

</template>

<script>

    export default {
        name: "SettingsPermissions",
        data: () => ({
            snackbar: false,
            snackbarText: null
}),
        methods: {
            spanishSelected: function () {
                this.$i18n.locale = 'es';
                this.$forceUpdate();
                //this.$router.go(-1);
            },
            englishSelected: function () {
                this.$i18n.locale = 'en';
                this.$forceUpdate();
                //this.$router.go(-1);
            }
        },
        computed: {
            permissionsText: function () {
                return this.$i18n.t('messages.TEXT_PERMISSIONS');
            },
            locationText: function () {
                return this.$i18n.t('messages.TEXT_LOCATION');
            },
            notificationsText: function () {
                return this.$i18n.t('messages.TEXT_NOTIFICATIONS');
            },
            closeText: function () {
                return this.$i18n.t('messages.TEXT_CLOSE');
            },
            grantedText: function () {
                return this.$i18n.t('messages.TEXT_GRANTED');
            },
            notGrantedText: function () {
                return this.$i18n.t('messages.TEXT_NOT_GRANTED');
            },
            notificationsPermissionGranted: function () {
                return this.$store.getters['permissions/getIsPushNotificationPermissionGranted'];
            },
            locationPermissionGranted: function () {
                return this.$store.getters['permissions/getIsGeolocationPermissionGranted'];
            }
}
}
</script>

<style scoped>

</style>
